/* colors and default size */
:root {
  --primaryColor: rgb(252, 39, 121);
  --primaryHover: rgb(228, 64, 166);
  --secondaryColor: rgb(150, 162, 188);
  --paragraphColor: rgb(127, 127, 175);
  --BgColor: rgb(109, 6, 68);
  --formColor: rgb(2, 15, 29);
  --projectCardColor: rgb(83, 8, 53);
  --textColor: #dae4fb;
  --headerHeight: 3rem;
}

.footer {
  width: 100%;
  text-align: center;
  background: var(--projectCardColor);
  margin-top: 1rem;
  padding: 1rem 0;
}
.footer span {
  text-align: center;
  color: var(--secondaryColor);
  opacity: 0.5;
  font-size: 13px;
  font-weight: 600;
} /*# sourceMappingURL=footer.css.map */
